import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder } from '../../../../node_modules/@angular/forms';
import { MainService } from '../../providers/mainService';

@Component({
  selector: 'app-event-management',
  templateUrl: './event-management.component.html',
  styleUrls: ['./event-management.component.css']
})
export class EventManagementComponent implements OnInit {
  total: any
  eventForm: FormGroup;
  paginationData: any={};
  srNo: number;
  Event:any=[];
  pageSize:any= {};
  pageNo: any = 1;
  limit: any;
 
  constructor(public fb: FormBuilder, public service : MainService) {
    this.eventForm = fb.group({
      'search':['']
    })
   }
  ngOnInit() {
    this.getAllEvent()
  }

    //****************************GET ALL Events *************************************** */
    changePage(page){
    this.pageNo= page
    this.getAllEvent()
      }
    
      filter(){
        this.pageNo = 1;
        this.getAllEvent()
      }
  getAllEvent() {
    let postData = {
            search: this.eventForm.controls['search'].value,
            pageNumber: this.pageNo,
          
          }
    this.service.postApi(`event/getAllEvents`,postData,1).subscribe(response => {
      if (response['response_code'] == 200) {
        this.Event = response[`result`]
        this.paginationData = response[`paginationData`]
        this.limit = response[`paginationData`].limit
        this.srNo = (this.pageNo - 1) * this.paginationData.limit
       
        
      }
      else {
              this.Event = [];
              this.paginationData = {total: 0, limit: 10}
              }
    }, error => {
      this.service.toastErr('Something Went Wrong')
    })
  }
  
}