import { Component, OnInit } from '@angular/core';
import { MainService } from '../../providers/mainService';
import { Router } from '../../../../node_modules/@angular/router';
import { FormBuilder, FormGroup } from '../../../../node_modules/@angular/forms';

@Component({
  selector: 'app-content-management-edit',
  templateUrl: './content-management-edit.component.html',
  styleUrls: ['./content-management-edit.component.css']
})
export class ContentManagementEditComponent implements OnInit {
  
  public editorValue: string = '';
  controls: string;
  editForm:FormGroup;
  viewData:any;
  constructor(private service: MainService,private route: Router, private formBuilder: FormBuilder){
  
    this.editForm = this.formBuilder.group({
      data: ['']
    })
  }
  ngOnInit() {
    this.controls = localStorage.getItem('control')
    this.controls = localStorage.getItem('control-edit')
    this.service.getApi(`static/getStaticContent`, 1).subscribe(response => {
          if (response['response_code'] == 200) {
    if( this.controls == 'About Us'){
      this.viewData = response['result'][0].aboutUs
        }
        else if( this.controls == 'Terms And Conditions'){
          this.viewData = response['result'][0].termsAndConditions
          
        }
        else if( this.controls == 'Privacy Policy'){
          this.viewData = response['result'][0].privacyPolicy
        }
        this.editForm.patchValue({
          data: this.viewData
        })
                }
          else {
            this.service.toastErr(response['response_message'])
          }
        }, error => {
          this.service.toastErr('Something Went Wrong')
        })

  }
  
 
    save(){
      let postData;
      if(localStorage.getItem('control-edit')== "Terms And Conditions"){
      
        postData={
          "termsAndConditions":this.editForm.value.data,
              
        }

      }
    else if(localStorage.getItem('control-edit')== "Privacy Policy"){
      postData={
       
        "privacyPolicy":this.editForm.value.data,
        
      }


    }
    else if(localStorage.getItem('control-edit')== "About Us"){
      postData={       
        "aboutUs":this.editForm.value.data,
         
      }}   
      
  
      this.service.postApi('static/updateStatic',postData,1).subscribe(response=>{
        if(response['response_code']==200){
            this.route.navigate(['/content-management'])
        }
        else{
          this.service.toastErr(response['response_message'])
        }
      },error=>{
        this.service.toastErr('Something Went Wrong')
      })
    }

   
}




