import { Component, OnInit } from '@angular/core';
import { MainService } from '../../providers/mainService';
import { Router } from '@angular/router';
import { FormGroup, FormBuilder } from '../../../../node_modules/@angular/forms';
declare var $: any;
@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.css']
})
export class DashboardComponent implements OnInit {
  total: any
  dashboardForm: FormGroup;
  pageNo: any = 1;
  collection = [];
  customer: any = [];
  id: any = '';
  srNo: number;
  srNo1: number;
  paginationData: any = {};
  status: any;
  obj: any = {};
  modified = []

  constructor(private router: Router, public service: MainService, public fb: FormBuilder) {
    this.dashboardForm = this.fb.group({
      'search': [''],
      'sort': [''],
      'pageNumber': ['']
    })

    for (let i = 1; i <= 10; i++) {
      this.collection.push(`item ${i}`);
    }
  }
  ngOnInit() {
    this.search()

  }

  view(l) {
    localStorage.setItem('_id of customer', this.customer[l]._id);
    this.router.navigate(['/userDetails']);
  }



  userStatus(item) {

    this.status = item.status;
    if (this.status == "ACTIVE" || "BLOCK") {

      let delData = { '_id': item._id }
      this.service.postApi(`admin/blockUser`, delData, 1).subscribe(response => {
        if (response['response_code'] == 200) {
        }
        else {
          this.service.toastErr(response['response_message'])
        }
      }, error => {
        this.service.toastErr('Something Went Wrong')



      })


    }
  }


  edit(l) {
    localStorage.setItem('_id of customer', this.customer[l]._id);
    this.router.navigate(['./editUserDetail']);
  }

  userdel(l) {
    localStorage.setItem('_id of customer', this.customer[l]._id);
    $('#delete').modal('show');
  }
  delete(l) {
    let delData = { '_id': localStorage.getItem('_id of customer') }
    this.service.postApi(`admin/deleteUser`, delData, 1).subscribe(response => {
      if (response['response_code'] == 200) {
        localStorage.removeItem('_id of customer')
        this.search()
      }
      else {
        this.service.toastErr(response['response_message'])
      }
    }, error => {
      this.service.toastErr('Something Went Wrong')



    })

    $('#delete').modal('hide');

  }

  search() {
    let postData = {
      search: this.dashboardForm.controls['search'].value,
      status: this.dashboardForm.controls['sort'].value,
      userType: "CUSTOMER",
      pageNumber: this.pageNo    

    }

    for (let val in postData) {

      if (postData[val] == '') {
        delete postData[val]
      }
    }
    this.service.postApi(`user/searchCustomerFilter`, postData, 1).subscribe(response => {
      if (response['response_code'] == 200) {
        this.customer = response['result'];
        this.modified = this.customer
      
        for (let i = 0; i < this.customer.length; i++) {
          if (this.customer[i].status == 'ACTIVE') {
            this.modified[i].status = true

          }

          else if (this.customer[i].status == 'BLOCK') {
            this.modified[i].status = false

          }
        }
        this.paginationData = response['paginationData']
        this.srNo = (this.pageNo - 1) * this.paginationData.limit

      }

      else {
        
        this.paginationData = {total: 0, limit: 10}
        this.modified = [];
         }
    }, error => {
    })



  }
  filter(){
    this.pageNo = 1;
    this.search()
  }
  changePage(page) {
    this.pageNo = page
    this.search()
  }

}

